/* mobile full height fix */
:root {
  --app-height: 100%;
  --header-height: 0;
}

body {
  overflow-x: hidden;
  position: relative;
  height: 100%;
  background-color: #fff;
}
body::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  border-radius: 0;
}
body::-webkit-scrollbar-thumb {
  background: #F96791;
  border-radius: 0;
  transition: background-color 0.3s;
}
body::-webkit-scrollbar-thumb:hover {
  background: #6FDE9B;
}
body::-webkit-scrollbar-track {
  background: #F3F5F6;
  border-radius: 0;
}
body {
  scrollbar-face-color: #F96791;
  scrollbar-track-color: #F3F5F6;
}

.ccontainer {
  width: 100%;
  margin: 0 auto;
  max-width: 1140px;
  padding: 0 15px;
}

.cheader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  background-color: #fff;
  border-bottom: 1px solid transparent;
  transition: border-color 0.3s;
}
.cheader .ccontainer {
  max-width: 1440px;
}
.cheader__top {
  padding: 16px 0;
  background: linear-gradient(75.57deg, rgba(250, 97, 115, 0.3) 15.62%, rgba(22, 147, 220, 0.3) 57.16%, rgba(0, 199, 83, 0.3) 94.08%);
}
.cheader__top .ccontainer {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1;
}
.cheader__top .ccontainer .cheader-txt {
  font-family: DroidSans, sans-serif;
  font-weight: 700;
  font-size: 15px;
  line-height: 21px;
  color: #343537;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  margin-right: 31px;
}
.cheader__top .ccontainer .cheader-link {
  display: inline-block;
  background-color: #fff;
  color: #F96791;
  text-decoration: none;
  font-family: DroidSans, sans-serif;
  font-weight: 700;
  font-size: 12px;
  text-transform: uppercase;
  text-align: center;
  padding: 12px 24px;
  border-radius: 100px;
  margin-right: 35px;
  transition: background-color 0.3s, color 0.3s;
  letter-spacing: 0.02em;
}
.cheader__top .ccontainer .cheader-link:hover {
  color: #fff;
  background-color: #F96791;
}
.cheader__top .cheader-close {
  position: absolute;
  top: calc(50% - 12px);
  right: 35px;
  font-size: 24px;
  line-height: 24px;
  color: rgba(52, 53, 55, 0.5);
  transition: color 0.3s;
  z-index: 1;
}
.cheader__top .cheader-close:hover {
  color: #343537;
}
.cheader__main {
  padding: 15px 0;
  transition: padding-top 0.3s, padding-bottom 0.3s;
  /* settings */
  /* slide animate */
}
.cheader__main .ccontainer {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.cheader__main .logo-link {
  display: block;
  flex: 0 0 auto;
  width: auto;
}
.cheader__main .logo-link .logo-img {
  display: block;
  width: auto;
  max-width: 100%;
}
.cheader__main .mmenu {
  flex: 0 0 auto;
  width: auto;
  margin-left: 40px;
}
@media (max-width: 1200px) {
  .cheader__main .mmenu {
    margin-left: 30px;
  }
}
@media (max-width: 1024px) {
  .cheader__main .mmenu {
    display: none;
  }
}
.cheader__main .mmenu .mmenu-list {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.cheader__main .mmenu .mmenu-list__item {
  display: block;
  width: auto;
  margin-left: 40px;
  padding: 10px 0;
  position: relative;
}
@media (max-width: 1200px) {
  .cheader__main .mmenu .mmenu-list__item {
    margin-left: 30px;
  }
}
.cheader__main .mmenu .mmenu-list__item:nth-of-type(1) {
  margin-left: 0;
}
.cheader__main .mmenu .mmenu-list__item:hover .mmenu-list__sublink {
  color: #000;
}
.cheader__main .mmenu .mmenu-list__item:hover .submenu-list {
  display: block !important;
}
.cheader__main .mmenu .mmenu-list__link {
  font-family: DroidSans, sans-serif;
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.045em;
  color: #000;
  text-transform: uppercase;
  text-decoration: none;
}
.cheader__main .mmenu .mmenu-list__sublink {
  display: inline-block;
  color: #c4c4c4;
  font-size: 8px;
  line-height: 8px;
  margin-left: 3px;
  position: relative;
  bottom: 1px;
  transition: color 0.3s;
}
.cheader__main .mmenu .mmenu-list .submenu-list {
  position: absolute;
  top: 100%;
  left: 0;
  width: auto;
  min-width: 300px;
  background-color: #fff;
  padding: 20px;
  border: 1px solid #eee;
}
.cheader__main .mmenu .mmenu-list .submenu-list__item {
  display: block;
  margin-top: 10px;
}
.cheader__main .mmenu .mmenu-list .submenu-list__item:nth-of-type(1) {
  margin-top: 0;
}
.cheader__main .mmenu .mmenu-list .submenu-list__link {
  display: inline-block;
  font-family: DroidSans, sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-decoration: none;
  color: #343537;
  position: relative;
}
.cheader__main .mmenu .mmenu-list .submenu-list__link:before {
  content: "";
  display: block;
  width: 0;
  height: 1px;
  background-color: #F96791;
  transition: width 0.3s;
  position: absolute;
  bottom: 0;
  left: 0;
}
.cheader__main .mmenu .mmenu-list .submenu-list__link:hover:before {
  width: 100%;
}
.cheader__main .profile-links {
  flex: 0 0 auto;
  width: auto;
  margin-left: auto;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.cheader__main .profile-links__item {
  display: inline-block;
  margin-left: 20px;
}
.cheader__main .profile-links__item.support-item {
  font-size: 28px;
  line-height: 28px;
  color: #000;
  position: relative;
  top: -1px;
}
.cheader__main .profile-links__item.cart-item {
  position: relative;
  font-size: 25px;
  line-height: 28px;
  color: #000;
}
.cheader__main .profile-links__item.cart-item .cart-count {
  position: absolute;
  top: -5px;
  right: -5px;
  background-color: #F96791;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-family: DroidSans, sans-serif;
  font-size: 12px;
  line-height: normal;
  min-width: 18px;
  height: 18px;
  border-radius: 50%;
}
.cheader__main .profile-links .personal-block {
  margin-left: 20px;
}
@media (max-width: 1024px) {
  .cheader__main .profile-links .personal-block {
    margin-left: 40px;
  }
}
.cheader__main .profile-links .personal-block .enter-link {
  display: inline-block;
  background-color: #F96791;
  border-radius: 100px;
  font-family: DroidSans, sans-serif;
  font-weight: 700;
  font-size: 12px;
  line-height: 20px;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  padding: 14px 25px;
  letter-spacing: 0.025em;
  transition: background-color 0.3s, color 0.3s;
}
@media (max-width: 1200px) {
  .cheader__main .profile-links .personal-block .enter-link {
    padding: 14px 15px;
  }
}
.cheader__main .profile-links .personal-block .enter-link:hover {
  background-color: #EE4F62;
}
.cheader__main .profile-links .personal-block .sing-in-link {
  display: inline-block;
  border: 2px solid #F96791;
  border-radius: 100px;
  font-family: DroidSans, sans-serif;
  font-weight: 700;
  font-size: 12px;
  line-height: 20px;
  color: #F96791;
  text-align: center;
  text-decoration: none;
  padding: 12px 23px;
  margin-left: 25px;
  letter-spacing: 0.025em;
  transition: background-color 0.3s, color 0.3s;
}
@media (max-width: 1200px) {
  .cheader__main .profile-links .personal-block .sing-in-link {
    padding: 12px 15px;
    margin-left: 10px;
  }
}
.cheader__main .profile-links .personal-block .sing-in-link:hover {
  background-color: #F96791;
  color: #fff;
}
.cheader__main .hamburger {
  padding: 0;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  overflow: visible;
  position: relative;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  z-index: 20;
  margin: 0 0 0 20px;
}
@media (max-width: 1024px) {
  .cheader__main .hamburger {
    display: block !important;
  }
}
.cheader__main .hamburger.is-active .hamburger-inner {
  background-color: #000;
}
.cheader__main .hamburger.is-active .hamburger-inner::before, .cheader__main .hamburger.is-active .hamburger-inner::after {
  background-color: #000;
}
.cheader__main .hamburger-box {
  width: 32px;
  height: 32px;
  display: inline-block;
  position: relative;
}
.cheader__main .hamburger-inner {
  display: block;
  top: 50%;
  width: 32px;
  height: 2px;
  background-color: #000;
  border-radius: 4px;
  position: absolute;
  transition-timing-function: ease;
  transition: transform 0.15s;
}
.cheader__main .hamburger-inner::before, .cheader__main .hamburger-inner::after {
  width: 32px;
  height: 2px;
  background-color: #000;
  border-radius: 4px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}
.cheader__main .hamburger-inner::before, .cheader__main .hamburger-inner::after {
  content: "";
  display: block;
}
.cheader__main .hamburger-inner::before {
  top: -10px;
}
.cheader__main .hamburger--slider .hamburger-inner {
  top: 7px;
}
.cheader__main .hamburger--slider .hamburger-inner::before {
  top: 8px;
  right: 0;
  width: 32px;
  transition-property: transform, opacity;
  transition-timing-function: ease;
  transition-duration: 0.15s;
}
.cheader__main .hamburger--slider .hamburger-inner::after {
  top: 16px;
}
.cheader__main .hamburger--slider.is-active .hamburger-inner {
  transform: translate3d(4px, 8px, 0) rotate(45deg);
  height: 2px;
  width: 25px;
}
.cheader__main .hamburger--slider.is-active .hamburger-inner::before {
  transform: rotate(-45deg) translate3d(-5.71429px, -6px, 0);
  opacity: 0;
}
.cheader__main .hamburger--slider.is-active .hamburger-inner::after {
  transform: translate3d(0, -16px, 0) rotate(-90deg);
  height: 2px;
  width: 25px;
}
.cheader.scroll {
  border-bottom: 1px solid #eee;
}
.cheader.scroll .cheader__main {
  padding: 0;
}
.cheader + main, .cheader + div {
  margin-top: var(--header-height);
}

/* main page */
.cmain-block {
  padding-top: 35px;
}
.cmain-block__content {
  display: flex;
  align-items: stretch;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.cmain-block__content__left-side {
  flex: 0 0 30%;
  max-width: 30%;
  position: relative;
  z-index: 1;
}
.cmain-block__content__right-side {
  flex: 0 0 70%;
  max-width: 70%;
  max-height: 100%;
  margin-top: -33px;
}
.cmain-block__content__right-side .cmain-slider {
  max-height: 505px;
  right: -10px;
  padding-right: 30px;
}
.cmain-block__content__right-side .cmain-slider.swiper-vertical .swiper-wrapper {
  flex-direction: column;
}
.cmain-block__content__right-side .cmain-slider.swiper-vertical .swiper-slide {
  flex: 0 0 auto;
  height: auto;
  width: auto;
}
.cmain-block__content__right-side .cmain-slider .slide-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 86px;
  height: 86px;
  border-radius: 50%;
  background: #FFFFFF;
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.8);
  color: #F96791;
  position: absolute;
  top: calc(50% - 5px);
  left: calc(50% + 2px);
  text-decoration: none;
  transition: transform 0.3s;
}
.cmain-block__content__right-side .cmain-slider .slide-link:hover {
  transform: scale(1.1);
}
.cmain-block__content__right-side .cmain-slider .slide-link .icon {
  position: relative;
  left: 3px;
  top: -2px;
}
.cmain-block__content__right-side .cmain-slider .slide-link .icon:before {
  line-height: normal;
}
.cmain-block__content__right-side .cmain-slider .slide-image {
  display: block;
  width: auto;
  max-width: 100%;
  margin: 0 0 0 auto;
}
.cmain-block__content__right-side .cmain-slider .cmain-pagination {
  position: absolute;
  top: 0;
  right: 0px;
  height: 100%;
  width: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 1;
}
.cmain-block__content__right-side .cmain-slider .cmain-pagination .swiper-pagination-bullet {
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
