@import "settings"
@import "_mixins/_mixins"
@import "_fonts/fonts_list"
@import "_fonts/fontello"                                                                                               // if you use fontello fonts
@import "_components/reset"
@import "_components/header"

/* main page */
.cmain-block
  padding-top: 35px
  &__content
    display: flex
    align-items: stretch
    justify-content: flex-start
    flex-wrap: wrap
    &__left-side
      flex: 0 0 30%
      max-width: 30%
      position: relative
      z-index: 1
    &__right-side
      flex: 0 0 70%
      max-width: 70%
      max-height: 100%
      margin-top: -33px
      .cmain-slider
        max-height: 505px
        right: -10px
        padding-right: 30px
        &.swiper-vertical
          .swiper-wrapper
            flex-direction: column
          .swiper-slide
            flex: 0 0 auto
            height: auto
            width: auto
        .slide-link
          display: flex
          align-items: center
          justify-content: center
          width: 86px
          height: 86px
          border-radius: 50%
          background: #FFFFFF
          box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.8)
          color: $pink-color
          position: absolute
          top: calc(50% - 5px)
          left: calc(50% - -2px)
          text-decoration: none
          transition: transform .3s
          &:hover
            transform: scale(1.1)
          .icon
            position: relative
            left: 3px
            top: -2px
            &:before
              line-height: normal
        .slide-image
          display: block
          width: auto
          max-width: 100%
          margin: 0 0 0 auto
        .cmain-pagination
          position: absolute
          top: 0
          right: 0px
          height: 100%
          width: 10px
          display: flex
          flex-direction: column
          align-items: center
          justify-content: center
          z-index: 1
          .swiper-pagination-bullet
            display: block
            width: 10px
            height: 10px
            border-radius: 50%
            background-color: #D9D9D9
            transition: background-color .3s
            margin: 5px 0
            opacity: 10
            &.swiper-pagination-bullet-active
              background-color: $pink-color
  &__title
    flex: 0 0 auto
    width: auto
    max-width: 645px
    margin-right: auto
    font-family: Veles, sans-serif
    font-weight: 700
    font-size: 30px
    line-height: 48px
    color: $txt-color
    position: relative
    z-index: 3
  &__main-btn
    display: inline-block
    margin-top: 23px
    background-color: $pink-color
    border-radius: 60px
    font-family: $default-font
    font-weight: 700
    font-size: 12px
    line-height: 22px
    letter-spacing: .035em
    text-decoration: none
    text-transform: uppercase
    text-align: center
    color: #fff
    padding: 13px 52px
    transition: background-color .3s
    &:hover
      background-color: $pink-darkness-color
  &__list
    margin-top: 40px
    ul
      margin: 0
      padding: 0
      list-style-type: none
      color: $txt-color
      font-family: $default-font
      font-size: 16px
      line-height: 21px
      letter-spacing: .03em
      li
        display: block
        position: relative
        padding-left: 32px
        margin-top: 21px
        &:nth-of-type(1)
          margin-top: 0
        &:before
          content: '\e801'
          font-family: Fontello
          font-style: normal
          font-weight: normal
          speak: never
          display: block
          margin: 0
          text-decoration: inherit
          text-align: center
          font-variant: normal
          text-transform: none
          font-size: 18px
          color: $pink-color
          position: absolute
          top: 1px
          left: -1px
  &__social
    margin-top: 85px
    display: flex
    flex-wrap: wrap
    .social-title
      display: inline-block
      white-space: nowrap
      font-family: $default-font
      font-weight: 700
      font-size: 14px
      line-height: 18px
      letter-spacing: .02em
      color: $pink-color
    .social-full
      display: inline-flex
      align-items: center
      justify-content: flex-start
      color: #000
      font-family: $default-font
      font-weight: 700
      font-size: 20px
      line-height: 26px
      text-decoration: none
      margin-top: 10px
      transition: color .3s
      span
        display: inline-block
        border: 2px solid #343537
        border-radius: 50px
        color: #343537
        font-size: 10px
        line-height: 13px
        text-transform: uppercase
        text-align: center
        padding: 8px 16px
        margin-left: 8px
        letter-spacing: .03em
        transition: color .3s, border-color .3s, background-color .3s
      &:hover
        color: $pink-color
        span
          background-color: $pink-color
          color: #fff
          border-color: $pink-color
    .social-youtube
      display: inline-flex
      align-items: center
      justify-content: flex-start
      color: #000
      font-family: $default-font
      font-weight: 700
      font-size: 20px
      line-height: 36px
      text-decoration: none
      transition: color .3s
      margin-top: 10px
      .icon
        color: #FF0000
        font-size: 28px
        line-height: 28px
        margin-right: 8px
      &:hover
        color: #FF0000
    .social-tg
      display: inline-flex
      align-items: center
      justify-content: flex-start
      color: #000
      font-family: $default-font
      font-weight: 700
      font-size: 20px
      line-height: 36px
      text-decoration: none
      transition: color .3s
      margin: 10px 0 0 25px
      .icon
        color: #0088cc
        font-size: 36px
        line-height: 36px
        margin-right: 5px
      &:hover
        color: #0088cc






