.cheader
  position: fixed
  top: 0
  left: 0
  width: 100%
  z-index: 10
  background-color: #fff
  border-bottom: 1px solid transparent
  transition: border-color .3s
  .ccontainer
    max-width: 1440px
  &__top
    padding: 16px 0
    background: $gradient
    .ccontainer
      display: flex
      align-items: center
      justify-content: center
      position: relative
      z-index: 1
      .cheader-txt
        font-family: $default-font
        font-weight: 700
        font-size: 15px
        line-height: 21px
        color: $txt-color
        letter-spacing: .08em
        text-transform: uppercase
        margin-right: 31px
      .cheader-link
        display: inline-block
        background-color: #fff
        color: $pink-color
        text-decoration: none
        font-family: $default-font
        font-weight: 700
        font-size: 12px
        text-transform: uppercase
        text-align: center
        padding: 12px 24px
        border-radius: 100px
        margin-right: 35px
        transition: background-color .3s, color .3s
        letter-spacing: .02em
        &:hover
          color: #fff
          background-color: $pink-color
    .cheader-close
      position: absolute
      top: calc(50% - 12px)
      right: 35px
      font-size: 24px
      line-height: 24px
      color: rgba($txt-color, .5)
      transition: color .3s
      z-index: 1
      &:hover
        color: $txt-color
  &__main
    padding: 15px 0
    transition: padding-top .3s, padding-bottom .3s
    .ccontainer
      display: flex
      align-items: center
      justify-content: flex-start
    .logo-link
      display: block
      flex: 0 0 auto
      width: auto
      .logo-img
        display: block
        width: auto
        max-width: 100%
    .mmenu
      flex: 0 0 auto
      width: auto
      margin-left: 40px
      @media (max-width: 1200px)
        margin-left: 30px
      @media (max-width: 1024px)
        display: none
      .mmenu-list
        display: flex
        align-items: center
        justify-content: flex-start
        &__item
          display: block
          width: auto
          margin-left: 40px
          padding: 10px 0
          position: relative
          @media (max-width: 1200px)
            margin-left: 30px
          &:nth-of-type(1)
            margin-left: 0
          &:hover
            .mmenu-list__sublink
              color: #000
            .submenu-list
              display: block !important
        &__link
          font-family: $default-font
          font-weight: 700
          font-size: 12px
          line-height: 16px
          letter-spacing: .045em
          color: #000
          text-transform: uppercase
          text-decoration: none
        &__sublink
          display: inline-block
          color: #c4c4c4
          font-size: 8px
          line-height: 8px
          margin-left: 3px
          position: relative
          bottom: 1px
          transition: color .3s
        .submenu-list
          position: absolute
          top: 100%
          left: 0
          width: auto
          min-width: 300px
          background-color: #fff
          padding: 20px
          border: 1px solid #eee
          &__item
            display: block
            margin-top: 10px
            &:nth-of-type(1)
              margin-top: 0
          &__link
            display: inline-block
            font-family: $default-font
            font-size: 16px
            line-height: 20px
            text-decoration: none
            color: $txt-color
            position: relative
            &:before
              content: ''
              display: block
              width: 0
              height: 1px
              background-color: $pink-color
              transition: width .3s
              position: absolute
              bottom: 0
              left: 0
            &:hover
              &:before
                width: 100%
    .profile-links
      flex: 0 0 auto
      width: auto
      margin-left: auto
      display: flex
      align-items: center
      justify-content: flex-start
      &__item
        display: inline-block
        margin-left: 20px
        &.support-item
          font-size: 28px
          line-height: 28px
          color: #000
          position: relative
          top: -1px
        &.cart-item
          position: relative
          font-size: 25px
          line-height: 28px
          color: #000
          .cart-count
            position: absolute
            top: -5px
            right: -5px
            background-color: $pink-color
            display: flex
            align-items: center
            justify-content: center
            color: #fff
            font-family: $default-font
            font-size: 12px
            line-height: normal
            min-width: 18px
            height: 18px
            border-radius: 50%


      .personal-block
        margin-left: 20px
        @media (max-width: 1024px)
          margin-left: 40px
        .enter-link
          display: inline-block
          background-color: $pink-color
          border-radius: 100px
          font-family: $default-font
          font-weight: 700
          font-size: 12px
          line-height: 20px
          color: #fff
          text-align: center
          text-transform: uppercase
          text-decoration: none
          padding: 14px 25px
          letter-spacing: .025em
          transition: background-color .3s, color .3s
          @media (max-width: 1200px)
            padding: 14px 15px
          &:hover
            background-color: $pink-darkness-color
        .sing-in-link
          display: inline-block
          border: 2px solid $pink-color
          border-radius: 100px
          font-family: $default-font
          font-weight: 700
          font-size: 12px
          line-height: 20px
          color: $pink-color
          text-align: center
          text-decoration: none
          padding: 12px 23px
          margin-left: 25px
          letter-spacing: .025em
          transition: background-color .3s, color .3s
          @media (max-width: 1200px)
            padding: 12px 15px
            margin-left: 10px
          &:hover
            background-color: $pink-color
            color: #fff
    @import "hamburger"
  &.scroll
    border-bottom: 1px solid #eee
    .cheader__main
      padding: 0
  & + main, & + div
    margin-top: var(--header-height)
    // transition: margin-top .3s tmp



